import React from "react"

export const title = "CSS"
export const body = (
  <>
    <h1>Regras</h1>

    <p>
      Criando um elemento <b>&lt;style&gt;</b> ou com um arquivo dedicado (aba <b>CSS</b> no editor ), podemos definir
      regras de estilos vlidas para todos os elementos de nossa pgina.
    </p>

    <h3>Seletores</h3>

    <p>
      Para definir regras gerais, precisamos utilizar seletores: marcaes que referenciam os atributos <b>id=""</b> e{" "}
      <b>class=""</b> ou a prpria tag do elemento.
    </p>

    <p>
      A estrutura de cada regra  <b>seletor</b> <b>&#123;</b> <i>estilos...</i> <b>&#125;</b>
    </p>

    <p>
      Para selecionar elementos, basta escrever o nome da prpria tag. Para selecionar um <b>id</b>, usamos <b>#</b> e
      para selecionar <b>classe</b>, usamos <b>.</b>
    </p>

    <p>
      Nomes de ids e classes devem ser escolhidos de acordo com a utilidade da regra. Enquanto id s pode aparecer uma
      vez por pgina, classes aparecem mltiplas vezes.
    </p>

    <code>
      <pre>{`<style>
  p {
    color: red;
  }
  #premium {
    color: gold;
  }
  .authorized {
    color: green;
  }
</style>
<body>
  <p>pendente</p>
  <p id="premium">premium</p>
  <p class="authorized">autorizado</p>
  <p>pendente</p>
  <p class="authorized">autorizado</p>
</body>`}</pre>
    </code>
    <p>
      <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "red" }}>pendente</p>
      <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "gold" }}>premium</p>
      <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "green" }}>autorizado</p>
      <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "red" }}>pendente</p>
      <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "green" }}>autorizado</p>
    </p>
    <h3>Comentrio</h3>
    <p>
      Em CSS,  possvel comentar trechos de cdigo e ignorar definies de propriedades embrulhando o contedo
      desconsiderado na notao <b>{"/*"}</b> <i>contedo comentado</i> <b>{"*/"}</b>.
    </p>

    <code>
      <pre>{`<style>
  p {
    /*
    color: pink; 
    ... tudo que estiver escrito aqui dentro ser ignorado
    */

    /* color: hotpink; somente essa linha ser ignorada */

    color: deeppink;
  }
</style>

<body>
  <p>qual ser meu tom de rosa?</p>
</body>`}</pre>
    </code>

    <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "deeppink" }}>qual ser meu tom de rosa?</p>
  </>
)
