import React from "react"

export const title = "CSS"
export const body = (
  <>
    <h1>Estilos</h1>

    <p>
      CSS (Cascading Style Sheet)  a linguagem responsvel por definir o <b>estilo</b> das pginas, descrevendo regras
      que definem a forma como elementos sero exibidos.
    </p>
    <p>
      Numa analogia com o corpo humano podemos dizer que, enquanto o HTML  o esqueleto que estrutura as partes do
      corpo, o CSS  a pele, os panos e os acessrios.
    </p>

    <h2>Propriedade</h2>
    <p>
      A estrutura de declarao de estilos  <b>propriedade: valor</b>. Por exemplo, para definir a cor de um texto como
      vermelho, declaramos o estilo:
    </p>

    <code>color: red;</code>

    <p>Essa declarao pode feita de duas formas: por elemento ou criando regras gerais.</p>

    <h3>elemento + style</h3>

    <p>
      Ao adicionar o atributo <b>style=""</b> em qualquer elemento HTML, podemos definir estilos vlidos somente para
      seu contedo.
    </p>

    <code>
      <pre>{`<body>
  <p style="color: red;">esse pargrafo  vermelho</p>
  <p>j esse no </p>
  <p>esse tambm no</p>
  <p style="color: blue;">esse pargrafo  azul</p>
  <p>mais um pargrafo padro</p>
  <p style="color: red;">e mais um pargrafo vermelho</p>
</body>`}</pre>
    </code>

    <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "red" }}>esse pargrafo  vermelho</p>
    <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px" }}>j esse no </p>
    <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px" }}>esse tambm no</p>
    <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "blue" }}>esse pargrafo  azul</p>
    <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px" }}>mais um pargrafo padro</p>
    <p style={{ textShadow: "1px 1px 1px black", lineHeight: "12px", color: "red" }}>e mais um pargrafo vermelho</p>
  </>
)
