import React from "react"

const boxStyle = {
  display: "inline-block",
  outline: "5px dashed black",
  fontSize: "50px",
  margin: "10px",
  lineHeight: "70px",
  height: "70px",
  width: "70px",
}

export const title = "CSS"
export const body = (
  <>
    <h1>Posicionamento</h1>

    <p>
      A propridade <b>position</b> permite especificar o mtodo de posicionamento que desejamos usar: <b>static</b>{" "}
      (comportamento padro), <b>relative</b>, <b>absolute</b> ou <b>fixed</b>.
    </p>

    <p>
      Ao usar mtodos diferentes do padro,  preciso utilizar as propriedades que definem coordenadas: <b>top</b>{" "}
      (cima), <b>right</b> (direita), <b>bottom</b> (baixo) e <b>left</b> (esquerda).
    </p>

    <h3>Static</h3>
    <p>
      At agora, vimos que elementos so posicionados em blocos (de cima-para-baixo) e em trechos (ocupando as linhas da
      esquerda-para-direita). Esse mtodo de posicionamento  o <b>static</b> (padro) e, aqui, as coordenadas no so
      consideradas.
    </p>

    <code>
      <pre>{`.box {
  display: inline-block;
  margin: 10px;
  width: 70px;
  height: 70px;
  outline: 5px dashed black;
}
.cat {
  position: static; /* redundante, pois o padro  static */
}

<div class="box cat"></div>
<div class="box cat"></div>
<div class="box cat"></div>
`}</pre>
    </code>

    <p>
      <div style={boxStyle}></div>
      <div style={boxStyle}></div>
      <div style={boxStyle}></div>
    </p>

    <hr />
    <h3>Relative</h3>
    <p>
      Com o posicionamento <b>relative</b>, podemos ajustar a posio do elemento em relao a sua posio original, de
      acordo com os pixels definidos em <b>top</b>, <b>right</b>, <b>bottom</b> e <b>left</b>. As posies dos elementos
      vizinhos no so afetadas pela mudana.
    </p>

    <code>
      <pre>{`.rat {
  position: relative;
  top: 10px;
  left: 10px;
}
.mouse {
  position: relative;
  bottom: 20px;
  right: 5px;
}

<div class="box cat"></div>
<div class="box rat"></div>
<div class="box cat"></div>
<div class="box mouse"></div>
<div class="box cat"></div>`}</pre>
    </code>

    <p>
      <div style={boxStyle}></div>
      <div
        style={{
          ...boxStyle,
          position: "relative",
          top: "10px",
          left: "10px",
        }}
      >
        
      </div>
      <div style={boxStyle}></div>
      <div
        style={{
          ...boxStyle,
          position: "relative",
          bottom: "20px",
          right: "5px",
        }}
      >
        
      </div>
      <div style={boxStyle}></div>
    </p>

    <hr />
    <h3>Absolute</h3>
    <p>
      No posicionamento <b>absolute</b>, podemos ajustar a posio do elemento com relao ao seu "elemento pai
      posicionado" (um elemento com posicionamento no-<b>static</b>). Caso esse elemento "referencial" no exista, as
      dimenses da prpria tela so consideradas. Os elementos vizinhos tem suas posies reajustadas pela mudana.
    </p>

    <code>
      <pre>{`.container {
  position: relative;
  background: darkviolet;
  padding: 10px;
}
.rabbit {
  position: absolute;
  top: -80px;
  right: 0px;
}
.poodle {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

<div class="container">
  <div class="box cat"></div>
  <div class="box rabbit"></div>
  <div class="box poodle"></div>
  <div class="box cat"></div>
</div>`}</pre>
    </code>

    <p
      style={{
        padding: "10px",
        position: "relative",
        background: "darkviolet",
      }}
    >
      <div style={boxStyle}></div>
      <div
        style={{
          ...boxStyle,
          position: "absolute",
          top: "-80px",
          right: "0px",
        }}
      >
        
      </div>
      <div
        style={{
          ...boxStyle,
          position: "absolute",
          bottom: "10px",
          right: "10px",
        }}
      >
        
      </div>
      <div style={boxStyle}></div>
    </p>

    <hr />
    <h3>Fixed</h3>
    <p>
      O posicionamento <b>fixed</b> permite ajustar a posio de um elemento com relao as dimenses da prpria tela. 
      o mesmo comportamento de <b>absolute</b> quando no h "referencial" e os elementos vizinhos tambm tem suas
      posies reajustadas.
    </p>

    <code>
      <pre>{`.monkey {
  position: fixed;
  bottom: 0;
  left: 20%;
  outline: none; /* o macaco escapou! */
}

<div class="box cat"></div>
<div class="box monkey"></div>
<div class="box cat"></div>`}</pre>
    </code>

    <p>
      <div style={boxStyle}></div>
      <div
        style={{
          ...boxStyle,
          outline: "none",
          position: "fixed",
          bottom: "0",
          left: "20%",
        }}
      >
        
      </div>
      <div style={boxStyle}></div>
    </p>
  </>
)
