import React from "react"

export const title = "HTML"
export const body = (
  <>
    <h1>Estrutura</h1>

    <p>
      HTML (Hypertext Markup Language)  a linguagem usada para definir a <b>estrutura</b> do contedo de uma pgina,
      declarando elementos como pargrafos, imagens, listas, links, tabelas, botes e outros.
    </p>

    <h2>Elementos</h2>

    <p>
      Para definir os <b>elementos</b> que compe uma pgina web, utilizamos tags: estruturas compostas por "palavras
      reservadas" que representam diferentes elementos.
    </p>
    <p>
      Tags so escritas seguindo a estrutura: abertura (<b>&lt;&gt;</b>), contedo e fechamento (<b>&lt;/&gt;</b>)
    </p>
    <code>&lt;elemento&gt; contedo &lt;/elemento&gt;</code>
    <p>
      O contedo de um pgina web  o contedo do elemento <b>&lt;body&gt;</b>
    </p>

    <code>
      <pre>{`<body>
    contedo
</body>`}</pre>
    </code>

    <h3>Pargrafo</h3>
    <p>
      Os blocos de texto que voc l nesse guia esto dentro de elementos <b>&lt;p&gt;</b>
    </p>
    <code>
      <pre>{`<p>Os blocos de texto que voc l nesse guia...</p>`}</pre>
    </code>

    <h3>Separador</h3>
    <p>
      O elemento <b>&lt;hr/&gt;</b> renderiza um separador na pgina. Note que, diferente do pargrafo, esse elemento
      "fecha em si mesmo", pois no aceita nenhum contedo.
    </p>

    <hr />

    <h3>Comentrio</h3>
    <p>
      Quando queremos ocultar algum trecho de cdigo, por ser algo relevante apenas pra quem desenvolve, usamos
      comentrios para no renderizar os elementos demarcados. A notao para comentrios em HTML  <b>&lt;!--</b>{" "}
      <i>contedo comentado</i> <b>--&gt;</b>
    </p>

    <code>
      <pre>{`<!-- <p>o que estiver aqui no aparece na pgina</p> -->`}</pre>
    </code>
  </>
)
