import React from "react";

export const title = "CodeBook";
export const body = (
  <>
    <h1>Ol!</h1>
    <p>
      Eu sou o <b>CodeBook</b>!
    </p>
    <p>
      Aqui voc pode aprender os fundamentos bsicos de <b>HTML</b> e <b>CSS</b>
      , tecnologias utilizadas para construir praticamente todas as coisas que
      voc encontra na <b>internet</b> (como esse tutorial) e tambm criar as
      suas primeiras <b>pginas web</b>.
    </p>

    <h2>Teoria + Prtica = Pgina</h2>
    <p>A plataforma  dividida em trs janelas:</p>

    <table>
      <tbody>
        <tr>
          <td
            style={{
              width: "100px",
              padding: "30px 10px",
              textAlign: "center",
              position: "relative",
            }}
          >
             material
            <b
              style={{
                display: "block",
                position: "absolute",
                top: "0",
                right: "3px",
              }}
            >
              &lt; &gt;
            </b>
          </td>
          <td
            style={{
              width: "100px",
              padding: "30px 10px",
              textAlign: "center",
              color: "#d5d7de",
              background: "#131417",
            }}
          >
             cdigo
          </td>
          <td
            style={{
              width: "100px",
              padding: "30px 10px",
              textAlign: "center",
              background: "white",
            }}
          >
             resultado
          </td>
        </tr>
      </tbody>
    </table>

    <p>
      Navegando pelas pginas do <b>material</b>, voc vai conhecendo os
      conceitos essenciais da web enquanto pode ir colocando o que aprendeu em
      prtica na janela de <b>cdigo</b> e acompanha o progresso da construo
      de sua pgina na janela de <b>resultado</b>.
    </p>

    <p>
      Se quiser salvar seu trabalho, clique no boto EDIT (canto superior
      direito) para acessar o <b>CodePen</b>. Crie sua conta e mantenha um
      portflio de pginas web!
    </p>

    <h2>Colabore</h2>
    <p>
      Caso tenha interesse, voc pode fazer parte desse projeto em diferentes
      frentes:
    </p>

    <ul>
      <li>revisando o material de ensino;</li>
      <li>adicionando novas pginas ao guia;</li>
      <li>melhorando a usabilidade da plataforma;</li>
      <li>recomendando o CodeBook pra algum;</li>
      <li>
        contribuindo com{" "}
        <a
          rel="noreferrer"
          href={process.env.REACT_APP_FEEDBACK_URL}
          target="_blank"
        >
          ideias ou feedbacks
        </a>
        !
      </li>
    </ul>

    <p>
      Acesse o{" "}
      <a
        target="_blank"
        rel="noreferrer"
        href={process.env.REACT_APP_CONTRIBUTING_URL}
      >
        GitHub
      </a>{" "}
      do projeto e saiba como colaborar.
    </p>
  </>
);
