import React from "react"

export const title = "HTML"
export const body = (
  <>
    <h1>Containers</h1>
    <p>
      Para organizar o cdigo ou aplicar estilos uniformes em sees da pgina, podemos usar elementos "containers" de
      dois tipos: blocos (<b>&lt;div&gt;</b>) ou trechos (<b>&lt;span&gt;</b>)
    </p>

    <p>
      Enquanto os blocos ocupam a linha inteira, os trechos tem a dimenso definida pelo seu contedo, como o nmero de
      caracteres de um texto.
    </p>
    <p>
      Para visualizar os containers, podemos usar a propriedade <b>background</b> em seu estilo.
    </p>
    <p>
      <b>background:</b> _____; <i>(palavra reservada para cor | cdigo hexadecimal | cdigo RGB)</i>
    </p>

    <code>
      <pre>{`.title {
  color: white;
  background: black;
}
.red {
  color: red;
  background: white;
}

<div class="title">
  a ltima palavra ser <span class="red">vermelha</span>
</div>`}</pre>
    </code>

    <div style={{ background: "black", color: "white" }}>
      a ltima palavra ser <span style={{ background: "white", color: "red" }}>vermelha</span>
    </div>

    <br />
    <h2>Semntica</h2>

    <p>
      Usando blocos (<b>&lt;div&gt;</b>), trechos (<b>&lt;span&gt;</b>) e estilos CSS, podemos construir praticamente
      qualquer coisa na web. No entanto,  considerado boa prtica usar elementos que garantam a semntica da pgina,
      isto , no apenas definam sua aparncia, mas tambm indiquem seu propsito ou sentido. Por exemplo:
    </p>

    <ul>
      <li>
        Usar <b>&lt;strong&gt;</b> e <b>&lt;em&gt;</b> (ao invs de <b>&lt;b&gt;</b> e <b>&lt;i&gt;</b>) para indicar
        nfase
      </li>
      <li>
        Usar ttulos e subttulos <b>&lt;h1&gt;</b>, <b>&lt;h2&gt;</b>,<b>&lt;h3&gt;</b>,<b>&lt;h4&gt;</b>,{" "}
        <b>&lt;h5&gt;</b>, <b>&lt;h6&gt;</b>
      </li>
      <li>
        Usar <b>&lt;header&gt;</b> para definir o cabealho da pgina
      </li>
      <li>
        Usar <b>&lt;nav&gt;</b> para agrupar elementos de navegao, como menus
      </li>
      <li>
        Usar <b>&lt;article&gt;</b> para artigos e textos principais
      </li>
      <li>
        Usar <b>&lt;section&gt;</b> para dividir contedo em sees
      </li>
      <li>
        Usar <b>&lt;figure&gt;</b> para agrupar imagens e legendas
      </li>
      <li>
        Usar <b>&lt;footer&gt;</b> para definir o rodap da pgina
      </li>
    </ul>
  </>
)
